body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  margin: 0;
  padding: 0;
}
.container {
  width: 80%;
  margin: auto;
  overflow: hidden;
  padding: 0 20px;
}
header {
  background: #333;
  color: #fff;
  padding-top: 30px;
  min-height: 70px;
  border-bottom: #0779e4 3px solid;
}
header a {
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
}
header ul {
  padding: 0;
  list-style: none;
}
header li {
  display: inline;
  padding: 0 20px 0 20px;
}
header #branding {
  float: left;
}
header #branding h1 {
  margin: 0;
}
header nav {
  float: right;
  margin-top: 10px;
}
header .highlight, header .current a {
  color: #0779e4;
  font-weight: bold;
}
header a:hover {
  color: #ffffff;
  font-weight: bold;
}
.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
@media(max-width: 768px) {
  header #branding,
  header nav,
  header nav li,
  .features .feature {
    float: none;
    text-align: center;
    width: 100%;
  }
  header {
    padding-bottom: 20px;
  }
  .container {
    width: 95%;
  }
}
@media (max-width: 600px) {
  .MuiGrid-item {
    flex-basis: 100%;
    max-width: 100%;
  }
  .recharts-responsive-container {
    height: 200px !important;
  }
  .MuiTableCell-root {
    padding: 8px;
  }
}
.dashboard-card {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.chart-container {
  margin-top: 20px;
  margin-bottom: 20px;
}
.fade-in {
  animation: fadeIn 1s;
}
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
